import React, { useState } from 'react';
import { Table, Input, InputNumber, Popconfirm, Form, Typography,Button,Spin } from 'antd';
import { useEffect } from 'react/cjs/react.development';
import Addrecord from './dialogbox';
import Grid from '@mui/material/Grid';

// import { render } from 'react-dom';

const EditableCell = ({
  editing,
  dataIndex,
  title,
  inputType,
  record,
  index,
  children,
  ...restProps
}) => {
  const inputNode = inputType === 'number' ? <InputNumber /> : <Input />;
  return (
    <td {...restProps}>
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{
            margin: 0,
          }}
          rules={[
            {
              required: true,
              message: `Please Input ${title}!`,
            },
          ]}
        >
          {inputNode}
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

const EditableTable = ({accounts,updateData}) => {
  const [form] = Form.useForm();
  const [data, setData] = useState(accounts);
  const [editingKey, setEditingKey] = useState('');
  const [showDialog,setDialog] =useState(false);
  const [loading, setLoading] = useState(false);

  const isEditing = (record) => record.Id === editingKey;

  
  
  const edit = (record) => {
    form.setFieldsValue({
      Name: record.Name,
      Phone: record.Phone,
      Site: record.Site,
    });
    setEditingKey(record.Id);
  };

  const cancel = () => {
    setEditingKey('');
  };
  const save = async (key) => {
    try {
      setLoading(true);
      setDialog(true);
      const row = await form.getFieldsValue();
      let obj={
        method:'PUT',
        url:'/update',
        data:{
          Id:key,
        ...row
        }
        
      }
      console.log(row)
      updateData(obj);
      setEditingKey('')

    } catch (errInfo) {
      console.log('Validate Failed:', errInfo);
    }
  };

  const del = async (key) => {
    console.log("delete-key", key);
    setLoading(true);
    let obj={
      method: 'DELETE',
      url:'/',
      data:{
        Id: key,
      }
    };
    const newData = [...data];
    const index = newData.findIndex((item) => key === item.Id);
    console.log("----index---",index);
    updateData(obj)
      if (index > -1) {
        const item = newData[index];
        console.log("item",item);
        newData.splice(index, 1);
        setData(newData);
        setEditingKey('');
      }
  
  }

  const add = async () => {
    setDialog(true);
    
  }

  const columns = [
    {
      title: 'Name',
      dataIndex: 'Name',
      width: '25%',
      editable: true,
    },
    {
      title: 'Phone',
      dataIndex: 'Phone',
      width: '15%',
      editable: true,
    },
    {
      title: 'Site',
      dataIndex: 'Site',
      width: '40%',
      editable: true,
    },
    {
      title: 'Actions',
      dataIndex: 'CleanStatus',
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <Typography.Link
              onClick={() => save(record.Id)}
              style={{
                marginRight: 8,
              }}
            >
              Save
            </Typography.Link>
            <Typography.Link
              style={{
                marginRight: 8,
              }}
            >
              <Popconfirm title="Sure to delete?" onConfirm={() => del(record.Id)}>
              <a> Delete</a>
            </Popconfirm>
             
            </Typography.Link>
            <Typography.Link title="Cancel" onClick={cancel}>
              <a>Cancel</a>
            </Typography.Link>
          </span>
        ) : (
          <Typography.Link disabled={editingKey !== ''} onClick={() => edit(record)}>
            Edit
          </Typography.Link>
        );
      },
    },
  ];
  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType:'text',
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });
  
  return (
    <Grid flexDirection="column" >
    <Grid item padding={2} display="flex" justifyContent="flex-end">

    <Addrecord updateData={updateData}/>
    </Grid>
    <Grid item>
      {loading ? <p>Please wait...</p> 
      :
      <Form form={form} component={false}>
      <Table
        components={{
          body: {
            cell: EditableCell,
          },
        }}
        bordered
        dataSource={data}
        columns={mergedColumns}
        rowClassName="editable-row"
        pagination={{
          onChange: cancel,
        }}
      />
    </Form>

      }
    
    </Grid>
    </Grid>
  );
};

export default EditableTable;